import { useState, useRef, useEffect } from 'react';
import './App.css';
import pretty from './utilities/prettyfy';

function App() {
  const [code, setCode] = useState("");
  const [codePrettify, setCodePrettify] = useState("");
  const inputNotPretty = useRef("inputNotPretty");
  const inputPretty = useRef("inputPretty");
  
  useEffect(() => {
    if (code == "") {
      inputNotPretty.current.value = "";
      setCodePrettify(pretty(code));
    } else {
      setTimeout(() => {
        setCodePrettify(pretty(code));
        selectAndCopy();
      }, 200);
    }   
  }, [code])
  
  const selectAndCopy = () => {
    setTimeout(() => {
      inputPretty.current.select();
    document.execCommand("copy");
    if (Notification.permission === "granted") {
      showNotification();
    } else if (Notification.permission !== "denied") {
      Notification.requestPermission().then(permission => {
        if(permission === "granted") {
          showNotification();
        }
      });
    }
    }, 100);
  };
  const showNotification = () => {
    // return new Notification("Text Copié !");
    return new Notification('Application Ipsum', { 
			body: 'Text Copié !',
			icon: require('./assets/copie.png')
		}); 
  };
  const dbClick = e => (e.detail == 2);
  const resetInput = e => dbClick(e) && setCode("");

  return (
    <div className="App">
      <h1>Beautiful Js</h1>
      <input type="text" placeholder='Colle Code javascript...' ref={inputNotPretty} onChange={ (e) => setCode(e.target.value) } onClick={ (e) => resetInput(e) } />
      <textarea ref={inputPretty} value={codePrettify} cols="50" rows="25" className={ codePrettify.length ? "active" : "" } ></textarea>
    </div>
  );
}

export default App;